html{
    font-size: 16px;
    line-height: 1.42857143;
}

.segment {
    padding: 50px 0px; // this adds padding to the top and bottom of the section
}

.btn{
    font-weight: 400;
    text-decoration: none;
    color: $primary-white;

    height: auto;
    line-height: 35px;
    padding: 5px 15px;
    text-transform: uppercase;
    font-size: 1.063rem;
    border-radius: 3px;
    white-space: normal;

    .main-buttons{
        padding: 10px 30px;
    }
}

// this 
.overflow-hidden {
    overflow: hidden; // This hides any content that overflows the container
    white-space: nowrap; // this keeps everything in  one line and stops is wrapping
}


//////////////////////////////////////////////////////////////////////////////////////////////
///BODY
//////////////////////////////////////////////////////////////////////////////////////////////

// this makes the manage consent button stick to the bottom of the page even when scrolling
// this needs to have button too for extra specificty, the .btn in base.scss would overide otherwise.
.sticky-consent {
    position: fixed;
    bottom: 0px;
    font-size: 12.8px;
    button{
       padding: 5px 10px;
       border: none;
       border-top-right-radius: 10px;
       background-color: #75788a;
       color: $primary-white;
    }
}

.sticky-help {
    position: fixed;
    bottom: 0px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%;
    font-size: 16px;

 }

